import React, { PropTypes } from 'react';
import { Link } from 'react-router';
import styled from 'styled-components';

import ProgressBar from './order/ProgressBar';
import InfosBlock from '../components/InfosBlock';
import { Wrapper, Block, Button3, Title2, Header2, Logo, Center, P, colors, mobileThresholdPixels }
  from '../components/styledComponents';
import LogoImg from '../assets/logo1.png';
import { trackEvent } from '../services/analytics';

const Message = styled.h2`
  font-weight: 200;
  font-size: 26px;
  margin-bottom: 20px;
  margin-top: 20px;
  color: ${colors.navy};
  text-align: center;
  @media (max-width: ${mobileThresholdPixels}) {
    margin-bottom: 3px;
    margin-top: 5px;
    font-size: 20px;
  }
`;

const ErrorMessage = styled.div`
  text-align: center;
`;

const renderRegisterSuccess = () => (
  <div>
    <Message>Votre inscription et votre commande ont bien été prises en compte</Message>
    <Center>
      <Link to={'/'}><Button3>{"Revenir vers la page d'accueil"}</Button3></Link>
    </Center>
  </div>
);

const renderLoggedInSuccess = () => (
  <div>
    <Message>Votre commande a bien été prise en compte</Message>
    <Center>
      <Link to={'/'}><Button3>{"Revenir vers la page d'accueil"}</Button3></Link>
    </Center>
  </div>
);

const errorMessages = {
  ValidationError: 'Cet email ou/et tél a déjà été utilisé',
  CVV_FORMAT_ERROR: 'Le code CVV doit être sur 3 ou 4 chiffres',
  CARD_NUMBER_FORMAT_ERROR: 'Numéro de carte erroné',
  EXPIRY_DATE_FORMAT_ERROR: 'Date d‘expiration de la carte non reconnu',
  PAST_EXPIRY_DATE_ERROR: 'Date d‘expiration de la carte dépassée',
  INACTIVE_CARD: `La carte bancaire n‘a pas pu être utilisée par notre prestataire bancaire.
    Merci d‘en utiliser une autre ou d'ajouter à nouveau cette carte.`,
  PAYMENT_ERROR: 'Une erreur de paiement a eu lieu',
};

const renderError = (hideError, errorType) => {
  const message = errorMessages[errorType] || 'Une erreur a eu lieu';
  return (
    <ErrorMessage>
      <P>{message}</P>
      <Button3 onClick={hideError}>Cliquer pour réessayer</Button3>
    </ErrorMessage>
  );
};

const Step6 = ({ submitRegister, isLoading, successRegisterOrder, error, total, customer,
submitLoggedInOrder, successLoggedInOrder, rdv, checkPromoCode, isPromoCodeLoading, errorPromoCode,
promoCode, totalPaid, deliveryFee, hideError, errorType }) => (
  <Wrapper>
    <Block id="infos">
      <Header2>
        <Link
          to={'/'} style={{ flex: 1, textAlign: 'center' }}
          onClick={() => trackEvent('click', 'home_step6')}
        >
          <Logo src={LogoImg} />
        </Link>
        <ProgressBar step={4} />
        {!successRegisterOrder &&
          <Title2>Mes informations</Title2>
        }
      </Header2>
      {isLoading && <Center><P>Chargement</P></Center>}
      {successLoggedInOrder && renderLoggedInSuccess()}
      {successRegisterOrder && renderRegisterSuccess()}
      {error && renderError(hideError, errorType)}
      <InfosBlock
        checkPromoCode={checkPromoCode}
        customer={customer}
        errorPromoCode={errorPromoCode}
        isPromoCodeLoading={isPromoCodeLoading}
        promoCode={promoCode}
        rdv={rdv}
        submitLoggedInOrder={submitLoggedInOrder}
        submitRegister={submitRegister}
        total={total}
        totalPaid={totalPaid}
        deliveryFee={deliveryFee}
        hide={successRegisterOrder || successLoggedInOrder || isLoading || error}
      />
    </Block>
  </Wrapper>
);

Step6.propTypes = {
  checkPromoCode: PropTypes.func.isRequired,
  customer: PropTypes.shape({}),
  error: PropTypes.bool,
  errorType: PropTypes.string,
  errorPromoCode: PropTypes.bool,
  isLoading: PropTypes.bool,
  isPromoCodeLoading: PropTypes.bool,
  hideError: PropTypes.func.isRequired,
  promoCode: PropTypes.shape({}),
  rdv: PropTypes.shape({}).isRequired,
  submitLoggedInOrder: PropTypes.func.isRequired,
  submitRegister: PropTypes.func.isRequired,
  successLoggedInOrder: PropTypes.bool,
  successRegisterOrder: PropTypes.bool,
  total: PropTypes.number.isRequired,
  totalPaid: PropTypes.number.isRequired,
  deliveryFee: PropTypes.number.isRequired,
};

Step6.defaultProps = {
  isLoading: false,
  successRegisterOrder: false,
  successLoggedInOrder: false,
  error: false,
  errorType: null,
  errorPromoCode: false,
  isPromoCodeLoading: false,
  customer: {},
  promoCode: {},
};

export default Step6;



