const validZipcodePrefix = '75';
const validZipcodes = ['92200', '92300', '92110', '92100', '92130', '93100'];

export default function checkError(zipcode) {
  if (typeof zipcode !== 'string' || zipcode.length !== 5) {
    return 'Merci d\'entrer un code postal à 5 chiffres';
  }
  if (!zipcode.startsWith(validZipcodePrefix) && !validZipcodes.includes(zipcode)) {
    return `Votre ville n'est pas encore couverte par notre service. Voici la liste des villes couvertes pour le moment:
      Paris, Neuilly, Levallois-Perret, Clichy, Issy-Lès-Moulineaux, Montrueil et Boulogne-Billancourt.`;
  }
  return '';
}



