import React, { PropTypes } from 'react';
import CardReactFormContainer from 'card-react';
import { Input, Form1, Row, ErrorText } from './styledComponents';

const CreditCard = ({ onChange, formError, cardNumber, expiryDate, cvc, error }) => (
  <CardReactFormContainer
    container="card-wrapper"
    formInputsNames={{
      number: 'CCnumber',
      expiry: 'CCexpiry',
      cvc: 'CCcvc',
      name: 'CCname',
    }}
    initialValues={{
      name: ' ',
      number: cardNumber,
      expiry: expiryDate,
      cvc,
    }}
    classes={{
      valid: 'valid-input', // optional — default 'jp-card-valid'
      invalid: 'invalid-input', // optional — default 'jp-card-invalid'
    }}
    formatting // specify whether you want to format the form inputs or not
  >

    <div id="card-wrapper" />
    <Form1>
      <Input
        placeholder="Numéro de carte" type="text" name="CCnumber"
        onChange={e => onChange('cardNumber', e.target.value)}
        error={formError && !cardNumber}
      />
      {error && <ErrorText>{error}</ErrorText>}
      <Row>
        <Input
          half placeholder="MM / AA (date d‘expiration)" type="text" name="CCexpiry"
          onChange={e => onChange('expiryDate', e.target.value)}
          error={formError && !expiryDate}
        />
        <Input
          half placeholder="CVC (3 chiffres au dos)" type="text" name="CCcvc"
          onChange={e => onChange('cvc', e.target.value)}
          error={formError && !cvc}
        />
      </Row>
    </Form1>

  </CardReactFormContainer>
);

CreditCard.propTypes = {
  onChange: PropTypes.func.isRequired,
  formError: PropTypes.bool,
  cardNumber: PropTypes.string,
  expiryDate: PropTypes.string,
  cvc: PropTypes.string,
  error: PropTypes.string,
};

CreditCard.defaultProps = {
  formError: false,
  cardNumber: '',
  expiryDate: '',
  cvc: '',
  error: '',
};

export default CreditCard;



